import * as React from "react";

interface ExperimentConfigsProps {
  blocking: string;
  correspondence: string;
  dataMode: string;
  indicator: string;
  taskMode: string;
  updateConfig: (id: string, value: string) => void;
  widgetMode: string;
}

export default class ExperimentConfigs extends React.Component<ExperimentConfigsProps, undefined> {
  constructor(props: ExperimentConfigsProps) {
    super(props);
    this.onChange = this.onChange.bind(this);
  }

  onChange(event: any) {
    this.props.updateConfig(event.target.id, event.target.value);
  }

  render() {
    const { blocking, correspondence, dataMode, indicator, taskMode, updateConfig, widgetMode } = this.props;
    return (
      <div id="configs">
        <label htmlFor="blocking">Blocking policy:</label>
        <select id="blocking" name="blocking" value={blocking} onChange={this.onChange}>
          <option value="NONE">NONE</option>
          <option value="RENDER">RENDER</option>
          <option value="SERIAL">SERIAL</option>
          <option value="STICKY">STICKY</option>
          <option value="MULTIPLES">MULTIPLES</option>
          <option value="MONOTONE">MONOTONE</option>
        </select>
        <label htmlFor="indicator">Indicator:</label>
        <select id="indicator" name="indicator" value={indicator} onChange={this.onChange}>
          <option value="NONE">NONE</option>
          <option value="SPINNER">SPINNER</option>
        </select>
        <label htmlFor="correspondence">Correspondence:</label>
        <select id="correspondence" name="correspondence" value={correspondence} onChange={this.onChange}>
          <option value="NONE">NONE</option>
          <option value="LABEL">LABEL</option>
        </select>
        <label htmlFor="taskMode">Task type:</label>
        <select id="taskMode" name="taskMode" value={taskMode} onChange={this.onChange}>
          <option value="THRESHOLD">THRESHOLD</option>
          <option value="EXTREMA">EXTREMA</option>
          <option value="TREND">TREND</option>
        </select>
        <label htmlFor="dataMode">Data Gen:</label>
        <select id="dataMode" name="dataMode" value={dataMode} onChange={this.onChange}>
          <option value="CANNED">CANNED</option>
        </select>
        <label htmlFor="widgetMode">Widget:</label>
        <select id="widgetMode" name="widgetMode" value={widgetMode} onChange={this.onChange}>
          <option value="FACET">FACET</option>
        </select>
      </div>
    );
  }
}


