import * as React from "react";

interface ExperimentTaskInstructionsProps {
  next: () => void;
}

export default class ExperimentTaskInstructions extends React.Component<ExperimentTaskInstructionsProps, undefined> {
  render() {
    return (
      <div>
        <h4>Instructions</h4>
        <p>The rest of the <b>tasks for this experiment will be the same as the one you practiced</b> during previous training, except you will not be shown the correct answer after submission. The question will be displayed as a reminder. In addition, the <b>stock price data and design of the visualization will change randomly between each task</b>. This is done intentionally, we want to see how your performance changes in the face of different designs. </p>

        <p>Please <b>answer the tasks as quickly and accurately as possible</b>. Please also try <b>not to take breaks during a task</b> (you may after clicking "Submit task" and before clicking "Continue to next task"). Our research is highly sensitive to task completion time.</p>

        <p>When you are ready, you can click the "Next" button below to start working on the rest of the tasks.</p>
        <div className="button-wrapper">
          <button onClick={this.props.next}>Next</button>
        </div>
      </div>
    );
  }
}


