import * as d3 from "d3";
import { Datum, shuffle } from "./utils";
import { TaskType, TaskMode, Widget } from "./experiment";
// import { instructionsStocks, trainingStocks, taskStocks, trainingStocksTrend, taskStocksTrend, stockShuffle } from "./stockData";
import { instructionsStocks, trainingStocks, taskStocks, trainingStocksTrend, taskStocksTrend, stockShuffle } from "./simpleData";
import { generateCannedTrends } from "./stockDataHelper";
/**
 * Returns a random integer between min and max (inclusive).
 * @param {number} min
 * @param {number} max
 */
export function getRandomInt(min: number, max: number, rng?: any) {
  if (rng === undefined) {
    rng = Math.random;
  }
  return Math.floor(rng() * (max - min + 1)) + min;
}

interface Delays {
  [index: string]: () => number;
}

export const DelayModes: Delays = {
  NONE: () => 1,
  // CONSTANT: () => 500,
  // CONSTANT_1: () => 1000,
  // CONSTANT_3: () => 3000,
  // CONSTANT_5: () => 5000,
  // UNIFORM: () => getRandomInt(0, 500),
  UNIFORM_1: () => getRandomInt(0, 1000),
  UNIFORM_3: () => getRandomInt(0, 3000),
  UNIFORM_5: () => getRandomInt(0, 5000),
};

interface FacetData {
  [index: string]: Datum[];
}

export const facetValues = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
const cannedValues = {
  [TaskMode.TASK]: shuffle([
    [ 25, 31, 36, 21, 35, 11, 60, 39, 22, 43, 43, 47 ],
    [ 6, 63, 55, 52, 26, 38, 88, 28, 69, 32, 59, 56 ],
    [ 44, 45, 16, 32, 58, 12, 24, 36, 13, 41, 31, 33 ],
    [ 26, 48, 86, 75, 18, 55, 76, 25, 72, 51, 6, 18 ],
    [ 23, 26, 28, 59, 59, 42, 15, 31, 74, 21, 51, 49 ],
    [ 10, 13, 20, 38, 41, 38, 19, 50, 66, 26, 44, 36 ],
    [ 37, 36, 33, 74, 11, 16, 85, 48, 40, 74, 9, 59 ],
    [ 44, 72, 9, 13, 51, 7, 30, 14, 19, 9, 37, 89 ],
    [ 19, 8, 87, 64, 56, 8, 7, 63, 28, 49, 59, 25 ],
  ]),
  [TaskMode.TRAINING]: shuffle([
    [ 76, 17, 65, 22, 37, 36, 77, 41, 16, 70, 30, 89 ],
    [ 36, 5, 12, 22, 18, 61, 14, 28, 23, 18, 22, 8 ],
    [ 54, 57, 28, 29, 55, 6, 57, 15, 9, 68, 9, 36 ],
  ]),
  [TaskMode.INSTRUCTIONS]: [[8, 9, 14, 17, 26, 34, 36, 47, 56, 60, 63, 70]],
};


// a global that determines the ordering of data per session
// the task number would index into the array to determine the delay
let delays: any[] = shuffle(Object.keys(DelayModes));

export function getCannedValues(taskMode: TaskMode, taskNum: number) {
  let length = cannedValues[taskMode].length;
  return cannedValues[taskMode][taskNum % length];
}

/**
 * This function first takes in the canned random values (cannedValues)
 * and generates distractor bars.
 * The length is implicitly based on the cannedValues length
 * HARDCODING, might need to update the state of experimentConfigs
 * @param
 */

function generateTaskData(taskMode: TaskMode, taskNum: number): FacetData {
  // this tells the function where to put the canned values
  const cannedIdx = 2;
  const facetCount = 12;
  const barCount = 5;
  const taskCount = 3;
  const startYear = 2011;
  let random = function(i: number): Datum { return {x: startYear + i, y: getRandomInt(5, 95) }; };
  let controlledNums = getCannedValues(taskMode, taskNum);

  let gendata = function(i: number) {
    let group = d3.range(barCount).map(random);
    group[cannedIdx].y = controlledNums[i];
    return group;
  };

  let facetdata: FacetData = {};
  for (let i: number = 0; i < facetValues.length; i++) {
    facetdata[facetValues[i]] = gendata(i);
  }
  return facetdata;
}

/**
 * keep track of whether data for a taskNum has been generated
 */
let dataCache: { [index: string]: FacetData } = {};

/**
 * Gets facet data using the given facet.
 * @param {number} taskNum
 * @param {string} facet
 */
function getFacetData(id: string, taskNum: number, facet: string, taskMode: TaskMode) {
  if (!(id in dataCache)) {
    dataCache[id] = generateTaskData(taskMode, taskNum);
  }
  return dataCache[id][facet];
}

/**
 * getAnswers gets the respective answer the a taskType
 */
export function getAnswers(taskNum: number, taskMode: TaskMode, taskType: TaskType) {
  // HARDCODING
  const threshold = 80;
  let seenData = getMiddleStockData(taskNum, taskMode);
  if (taskType === TaskType.THRESHOLD) {
    let r = seenData.filter((val: number) => val > threshold);
    // boolean, will hard code the threshold here..
    return r.length > 0 ? "YES" : "NO";
  } else if (taskType === TaskType.EXTREMA) {
    // argmax, assume no duplicates
    let max = Math.max(...seenData);
    return facetValues[seenData.indexOf(max)];
  } else {
    // find the index where the answer is 0 and highlight it by marking it as correct
    return getTrendAnswers(taskNum, taskMode).answer.indexOf(0);
    // return "0";
  }

}


/**
 * Gets aggregation data
 * Performs multiple levels of aggregation
 * Generated from a bounded random walk.
 * TODO
 */
function genAggregationData(xmin: number, xmax: number, ymin: number, ymax: number) {
  const data = [];
  for (let i = xmin; i <= xmax; i++) {
    data.push({x: i, y: getRandomInt(ymin, ymax)});
  }
  return data;
}

// Simulated aggregation dataset (needs improvement)
const AggregationDataset = genAggregationData(1915, 2015, 0, 30);

/**
 * Gets aggregation data within the selection range. Should implement some
 * sampling based on the range to make more realistic.
 * @param {[number, number]} selection
 */
function getAggregationData(selection: [number, number]) {
  const [xmin, xmax] = selection;
  // make the resulting points more detailed the smaller the range.
  return AggregationDataset.filter(d => xmin <= d.x && d.x <= xmax);
}

/**
 * Gets aggregation data using the min and max x values of the whole dataset
 */
export function getAllAggregationData() {
  const selection = d3.extent(AggregationDataset.map(d => d.x));
  return getAggregationData(selection);
}

export function stockDataset(taskMode: TaskMode) {
  let stocks;
  if (taskMode === TaskMode.INSTRUCTIONS) {
    stocks = instructionsStocks;
  } else if (taskMode === TaskMode.TRAINING) {
    stocks = trainingStocks;
  } else if (taskMode === TaskMode.TASK) {
    stocks = taskStocks;
  }
  return stocks;
}

function getStocksShuffle(taskMode: TaskMode, taskNum: number) {
  if (taskMode === TaskMode.TASK) {
    return stockShuffle[taskNum];
  }
  return taskNum;
}

function stockTrendAnswers(taskMode: TaskMode) {
  let stocks;
  if (taskMode === TaskMode.TRAINING) {
    stocks = trainingStocksTrend;
  } else if (taskMode === TaskMode.TASK) {
    stocks = taskStocksTrend;
  }
  return stocks;
}

function getStockData(id: string, taskNum: number, predicate: string, taskMode: TaskMode) {
  const stocks = stockDataset(taskMode);
  const shuffled = getStocksShuffle(taskMode, taskNum);
  let offset = facetValues.indexOf(predicate);
  let result = [];
  for (let i: number = 0; i < 5; i ++) {
    result.push({x: i + 2008, y: stocks[shuffled][i][offset]});
  }
  return result;
}

export function getMiddleStockData(taskNum: number, taskMode: TaskMode) {
  const stocks = stockDataset(taskMode);
  let mapped = getStocksShuffle(taskMode, taskNum);
  return stocks[mapped][2];
}

export function getTrendAnswers(taskNum: number, taskMode: TaskMode) {
  // uncomment the below if generating Trend data for stocks, a bit hacky.
  // generateCannedTrends(TaskMode.TASK);
  // console.log("training");
  // generateCannedTrends(TaskMode.TRAINING);
  const answers = stockTrendAnswers(taskMode);
  const shuffle = getStocksShuffle(taskMode, taskNum);
  return answers[shuffle];
}

/**
 * Sends request for data (currently simulated). Gets the correct data based
 * on the path and predicate, and returns a Promise with the requested data
 * after some specified random delay. The interaction id (itxid) is taken in
 * and passed back upon return
 * @param {string} id
 * @param {string} predicate
 * @param {string} delay
 * @param {number} itxid
 */
export function getData(id: string, widget: Widget, taskNum: number, predicate: string, delay: string, itxid: number, taskMode: TaskMode) {
  let data: Datum[];
  if (widget === Widget.Facet) {
    // facet widgets, predicate will be a string like "Jan" or "Feb"
    // data = getFacetData(id, taskNum, predicate, taskMode);
    data = getStockData(id, taskNum, predicate, taskMode);
  } else {
    // aggregate chart widgets, predicate will be a tuple like [2012,2015]
    data = getAggregationData(JSON.parse(predicate));
  }

  return new Promise((resolve, reject) => {
    if (delay === "NONE") {
      resolve({taskNum: taskNum, selection: predicate, data, itxid});
    } else {
      setTimeout(
        () => resolve({taskNum: taskNum, selection: predicate, data, itxid}),
        DelayModes[delay]()
      );
    }
  });
}



