/// <reference path="../../external/gpaas.d.ts" />

import * as React from "react";
import * as seedrandom from "seedrandom";

import ExperimentConfigs from "./ExperimentConfigs";
import ExperimentTaskInstructions from "./ExperimentTaskInstructions";
import ExperimentInstructions from "./ExperimentInstructions";
import ExperimentDemographics from "./ExperimentDemographics";
import ExperimentTraining from "./ExperimentTraining";
import ExperimentTask from "./ExperimentTask";
import ExperimentUsability from "./ExperimentUsability";

import { DelayModes } from "../lib/data";
import { TaskType, Encoding, Widget } from "../lib/experiment";

import {
  getUrlParameter,
  BlockingModes,
  IndicatorModes,
  CorrespondenceModes,
  TaskModes,
  DataModes,
  WidgetModes,
  BufferModes,
  getBetweenSubjectsParams,
  shuffle,
} from "../lib/experiment";

interface ExperimentContainerState {
  assignmentId: string;
  rng: seedrandom.prng;
  betweenSubjectsParams: any;
  withinSubjectsParams: any;
  step: string;
  trainingWithinSubjectsParams: any[];
  bonusReward: number;
  taskReward: number;
  taskNum: number;
  taskType: TaskType;
}

interface ExperimentContainerProps {
  debug: boolean;
}

export default class ExperimentContainer extends React.Component<ExperimentContainerProps, ExperimentContainerState> {
  constructor(props: ExperimentContainerProps) {
    super(props);
    this.generateParams = this.generateParams.bind(this);
    this.updateConfig = this.updateConfig.bind(this);
    this.setupExperiment = this.setupExperiment.bind(this);
    this.submitDemographic = this.submitDemographic.bind(this);
    this.start = this.start.bind(this);
    this.startTraining = this.startTraining.bind(this);
    this.finishTraining = this.finishTraining.bind(this);
    this.startTasks = this.startTasks.bind(this);
    this.submitTaskAnswer = this.submitTaskAnswer.bind(this);
    this.finishTasks = this.finishTasks.bind(this);
    let betweenSubjectsParams;
    if (props.debug) {
      betweenSubjectsParams = {
        blocking: "NONE",
        correspondence: "LABEL",
        dataMode: "CANNED",
        indicator: "SPINNER",
        taskMode: "TREND",
        widgetMode: "FACET",
      };
    } else {
      betweenSubjectsParams = getBetweenSubjectsParams();
    }
    const assignmentId = getUrlParameter("assignmentId");
    const rng = seedrandom(getUrlParameter("workerId"));

    const trainingWithinSubjectsParams = this.generateTrainingParams();
    const withinSubjectsParams = this.generateParams(rng);

    this.state = {
      assignmentId,
      rng,
      betweenSubjectsParams,
      withinSubjectsParams,
      step: "START",
      trainingWithinSubjectsParams,
      bonusReward: 1.00,
      taskReward: 0.20,
      taskNum: 1,
      taskType: TaskType.TREND,
    };
  }

  setupExperiment() {
    const { debug } = this.props;
    const { trainingWithinSubjectsParams, withinSubjectsParams } = this.state;

    function clearTask() {}
    function finish(opt: any) {
      const submitTo = getUrlParameter("turkSubmitTo");
      if (debug || submitTo === "sandbox") {
        alert("finished all tasks");
      } else {
        opt.submit(); // submit HIT
      }
    }
    function trainingTask() {}
    const trainingTasks = trainingWithinSubjectsParams.map(() => trainingTask);
    function viewTask() {}

    return {
      name: "trained_trend_trial",
      task: "trained_trend_trial",
      researcher: "jigsaw",
      numTasks: withinSubjectsParams.length,
      params: {
        params: [{ name: "p",  type: "UniformChoice", options: [0] }]
      },
      clearTask: clearTask,
      finish: finish,
      trainingTasks: trainingTasks,
      viewTask: viewTask,
    };
  }

  generateParams(rng?: seedrandom.prng) {
    let bufferSizeOps = [BufferModes.MEDIUM, BufferModes.SMALL, BufferModes.SINGLE];
    let colorOps = ["BLUE"];
    let delayOps = ["NONE", "UNIFORM_1", "UNIFORM_3", "UNIFORM_5"];
    let encodingOps = [Encoding.Color];
    let orderedOps = [true];
    let widgetOps = [Widget.Facet];
    let params = [];
    for (let b of bufferSizeOps) {
      for (let c of colorOps) {
        for (let d of delayOps) {
          for (let e of encodingOps) {
            for (let o of orderedOps) {
              for (let w of widgetOps) {
                params.push({
                  bufferSize: b,
                  color: c,
                  delay: d,
                  encoding: e,
                  ordered: o,
                  widget: w,
                });
              }
            }
          }
        }
      }
    }
    return shuffle(params, rng);
  }

  generateTrainingParams() {
    const encoding = Encoding.Color;
    const widget = Widget.Facet;
    const color = "BLUE";
    const ordered = true;

    return [
        {
          bufferSize: BufferModes.SINGLE,
          color,
          delay: "NONE",
          encoding,
          ordered,
          widget,
        },
        {
          bufferSize: BufferModes.SINGLE,
          color,
          delay: "UNIFORM_1",
          encoding,
          ordered,
          widget,
        },
        {
          bufferSize: BufferModes.SMALL,
          color,
          delay: "UNIFORM_3",
          encoding,
          ordered,
          widget,
        },
        {
          bufferSize: BufferModes.MEDIUM,
          color,
          delay: "UNIFORM_3",
          encoding,
          ordered,
          widget,
        },
      ];
  }

  updateConfig(id: string, value: string) {
    this.setState(prevState => {
      prevState.betweenSubjectsParams[id] = value;
      return prevState;
    });
  }

  start() {
    const e = gpaas.startExperiment(this.setupExperiment);
    if (e.setupSuccessful) {
      console.log("experiment started succesfully");
      this.setState({ step: "DEMOGRAPHIC" });
    } else {
      console.log("failed to start experiment");
    }
  }

  submitDemographic(data: any) {
    data = Object.assign({}, data);
    data.type = "DEMOGRAPHIC";
    console.log(data);
    gpaas.logData(data);
    this.setState({ step: "TRAIN" });
  }

  submitInstructions(data: any) {
    data = Object.assign({}, data);
    data.type = "INSTRUCTIONS";
    console.log(data);
    gpaas.logData(data);
  }

  startTraining() {
    this.setState({ step: "TRAINING_TASK" });
  }

  finishTraining() {
    this.setState({ step: "TASK_INSTRUCTIONS" });
  }

  startTasks() {
    this.setState({ step: "TASK" });
  }

  finishTasks() {
    this.setState({ step: "USABILITY" });
  }

  cancelTasks() {
    gpaas.cancelTasks();
    console.log("experiment cancelled");
  }

  submitUsability(data: any) {
    data.type = "USABILITY";
    console.log(data);
    gpaas.logData(data);
    gpaas.nextTask();
  }

  submitTrainingAnswer(data: any) {
    data.type = "TRAIN";
    console.log(data);
    gpaas.logData(data);
    gpaas.nextTraining();
  }

  submitTaskAnswer(data: any) {
    const { taskNum, withinSubjectsParams } = this.state;
    data.type = "TASK";
    console.log(data);
    gpaas.logData(data);
    if (taskNum < withinSubjectsParams.length) {
      gpaas.nextTask();
      this.setState(prevState => {
        return { taskNum: prevState.taskNum + 1 };
      });
    }
  }

  render() {
    const { assignmentId, betweenSubjectsParams, withinSubjectsParams, step, trainingWithinSubjectsParams, taskReward, bonusReward, taskType } = this.state;
    let configs = null;
    if (this.props.debug) {
      configs = (
        <ExperimentConfigs
          blocking={betweenSubjectsParams.blocking}
          correspondence={betweenSubjectsParams.correspondence}
          dataMode={betweenSubjectsParams.dataMode}
          indicator={betweenSubjectsParams.indicator}
          taskMode={betweenSubjectsParams.taskMode}
          updateConfig={this.updateConfig}
          widgetMode={betweenSubjectsParams.widgetMode}
        />
      );
    }
    let screen = null;
    if (step === "START") {
      screen = (
        <ExperimentInstructions
          bonusReward={bonusReward}
          onClick={this.start}
          taskReward={taskReward}
          disabled={assignmentId === "ASSIGNMENT_ID_NOT_AVAILABLE"}
        />
      );
    } else if (step === "DEMOGRAPHIC") {
      screen = <ExperimentDemographics onClick={this.submitDemographic} />;
    } else if (step === "TRAIN") {
      screen = (
        <ExperimentTraining
          logData={this.submitInstructions}
          next={this.startTraining}
          params={trainingWithinSubjectsParams}
        />
      );
    } else if (step === "TRAINING_TASK") {
      screen = (
        <ExperimentTask
          betweenSubjectsParams={betweenSubjectsParams}
          isTraining={true}
          next={this.finishTraining}
          submitAnswer={this.submitTrainingAnswer}
          withinSubjectsParams={trainingWithinSubjectsParams}
          taskType={taskType}
        />
      );
    } else if (step === "TASK_INSTRUCTIONS") {
      screen = <ExperimentTaskInstructions next={this.startTasks} />;
    } else if (step === "TASK") {
      screen = (
        <ExperimentTask
          betweenSubjectsParams={betweenSubjectsParams}
          cancel={this.cancelTasks}
          isTraining={false}
          next={this.finishTasks}
          submitAnswer={this.submitTaskAnswer}
          taskReward={taskReward}
          withinSubjectsParams={withinSubjectsParams}
          taskType={taskType}
        />
      );
    } else if (step === "USABILITY") {
      screen = <ExperimentUsability onClick={this.submitUsability} />;
    }
    return (
      <div>
        {/*configs*/}
        <div className="content-wrapper">
          {screen}
        </div>
      </div>
    );
  }
}



