import * as d3 from "d3";
import * as React from "react";

import Chart from "./Chart";

import { ColorScales, Datum } from "../lib/utils";

interface WidgetChartProps {
  id: string;
  bufferSize: number;
  datasets: { [index: string]: Datum[] };
  height?: number;
  marginBottom?: number;
  marginLeft?: number;
  marginRight?: number;
  marginTop?: number;
  selected: string[];
  updateSelection(id: string, selection: string): void;
  width?: number;
  colorScale: (i: number) => string;
}

/**
 * Represents an interactive chart widget. The chart uses the data from props
 * to render the appropriate dot(s). d3 is used to generate axes and scales.
 *
 * Users can select sections of the chart by brushing, which will call the
 * updateSelection method and selected sections will have a rect overlay.
 */
export default class WidgetBrush extends React.Component<WidgetChartProps, undefined> {
  static defaultProps = {
    height: 60,
    marginBottom: 30,
    marginLeft: 40,
    marginRight: 20,
    marginTop: 10,
    width: 400,
  };

  render() {
    const { bufferSize, datasets, height, id,
            marginBottom, marginLeft, marginRight, marginTop,
            selected, updateSelection, width, colorScale } = this.props;
    const sel = selected.map(s => JSON.parse(s)) as [number, number][];
    const innerWidth = width - marginLeft - marginRight;
    const innerHeight = height - marginTop - marginRight;

    // set the scales
    const ymin = d3.min(datasets["all"].map(d => d.y));
    const ymax = d3.max(datasets["all"].map(d => d.y));
    const xmin = d3.min(datasets["all"].map(d => d.x));
    const xmax = d3.max(datasets["all"].map(d => d.x));
    const x = d3.scaleLinear()
                .domain([xmin, xmax])
                .rangeRound([0, innerWidth]);
    const y = d3.scaleLinear()
                .domain([ymin, ymax])
                .rangeRound([innerHeight, 0]);


    // generate a brush that interacts with the chart
    const brush = d3.brushX()
                    .extent([[0, 0], [innerWidth, innerHeight]])
                    .on("end", function() {
                      const s = d3.brushSelection(this) as [number, number];
                      if (s !== null) {
                        updateSelection(id, JSON.stringify(s.map(x.invert)));
                      }
                    });

    // generate rectangles for old brushes
    const rects = [];
    for (let i = 0; i < selected.length; i++) {
      const s = sel[i];
      const xPt = x(s[0]);
      const w = x(s[1]) - x(s[0]);

      const c = colorScale(selected.length - 1 - i);
      const newC = d3.color(c);
      newC.opacity = 0.5;
      const cStr = newC.toString();

      rects.push(
        <rect key={i} x={xPt} y="0" width={w} height={innerHeight}
          fill={cStr} stroke="#666" shapeRendering="crispEdges"></rect>
      );
    }

    return (
      <Chart
        bufferSize={1}
        datasets={datasets}
        height={height}
        marginBottom={marginBottom}
        marginLeft={marginLeft}
        marginRight={marginRight}
        marginTop={marginTop}
        selected={["all"]}
        width={width}
        colorScale={colorScale}
      >
        {rects}
        <g ref={ g => d3.select(g).call(brush) }></g>
      </Chart>
    );
  }
}



