import * as React from "react";

interface ExperimentInstructionsProps {
  bonusReward: number;
  onClick: () => void;
  taskReward: number;
  disabled: boolean;
}

export default class ExperimentInstructions extends React.Component<ExperimentInstructionsProps, undefined> {
  render() {
    const { bonusReward, onClick, taskReward, disabled } = this.props;

    return (
      <div>
        <h4>Interactive Visualization User Study</h4>
        <p>This HIT consists of a series of visual analysis tasks. In these tasks, you will be presented with an interactive visualization dashboard of some stock price data. You will be asked to read values from this visualization, which requires interacting with it to reveal more information.</p>
        <p>The results of these tasks will be used for a research study on visual perception for interactive visualizations. The purpose of the study is to inform the design of interfaces for visual analytics tasks when there are delays. You may view our <a href="./policy.html" target="_blank">privacy policy</a> for more information. Your anonymized data may be used for publication.</p>
        <p>Your participation in this study is voluntary, and you will receive a ${taskReward.toFixed(2)} reward for each task completed. After completing all of the tasks, you will receive a ${bonusReward.toFixed(2)} completion bonus reward. You <b>must</b> complete all the tasks in this HIT to receive the completion bonus. If you do not wish to complete all the tasks, you can press the "Finish and claim current reward" button to exit this HIT and be paid your accumulated reward. You will only be paid once you complete all the tasks or click the finish button, given that the tasks are done with reasonable effort (i.e. no completely arbitrary guessing).</p>
        <p>If you are ready to begin, please click the button below.</p>
        <div className="button-wrapper">
          <button onClick={onClick} disabled={disabled}>I understand, and consent to participate</button>
        </div>
      </div>
    );
  }
}



