import * as React from "react";
import * as d3 from "d3";
import { Datum } from "../lib/utils";

interface TrendGlyphContainerProps {
  datasets: Datum[][];
  answerValues: number[];
  onClick: (event: any) => void;
  width?: number;
  height?: number;
  color?: string;
  disabled: boolean;
  isTraining: boolean;
}

interface TrendGlyphContainerState {
  selected: number;
}

export default class TrendGlyphContainer extends React.Component<TrendGlyphContainerProps, TrendGlyphContainerState> {
  static defaultProps = {
    width: 100,
    height: 100,
    color: "black",
  };

  constructor(props: TrendGlyphContainerProps) {
    super(props);
    this.state = {
      selected: -1,
    };
  }

  glyphOnClick = (event: any) => {
    if (!this.props.disabled) {
      this.setState({selected: parseInt(event.target.dataset.id)});
      this.props.onClick(event);
    }
  }

  render() {
    const { width, height, datasets, color, onClick, answerValues, disabled } = this.props;

    const x = d3.scaleLinear()
                .domain([0, 11])
                .rangeRound([0, width]);
    const y = d3.scaleLinear()
                .domain([-5, 105])
                // .domain([d3.min(data, d => d.y), d3.max(data, d => d.y)])
                .rangeRound([height, 0]);
    const line = d3.line<Datum>()
                    .curve(d3.curveCatmullRom)
                    // .curve(d3.curveNatural)
                    .x((d) => x(d.x))
                    .y((d) => y(d.y));

    let glyphs: JSX.Element[] = [];
    for (let i: number = 0; i < datasets.length; i ++) {
      let data = datasets[i];
      let answer = answerValues[i];

      let isSelected = (i === this.state.selected) ? " selected" : "";
      let isAnswer = (this.props.isTraining && this.props.disabled && (answer === 0)) ? " answer" : "";
      let isDisabled = disabled ? " disabled" : "";
      // FRAGILE: change data-value={answer} when using real stock data
      glyphs.push(
        <div
          key={i + "_glyph"}
          className={"glyph-wrapper inline-block" + isSelected + isAnswer + isDisabled}
          disabled={disabled}
        >
          <svg width={width} height={height}>
            <rect width={width} height={height} fill={"white"} stroke={"black"} data-value={i} data-id={i} onClick={this.glyphOnClick} />
            <path fill="none" stroke={color} strokeWidth="2" d={line(data)} ></path>
          </svg>
        </div>
      );
    }

    return (
      <div className="trend-input">
        {glyphs}
      </div>
    );
  }
}



