import * as React from "react";

import MergedContainer from "./MergedContainer";
import TrendGlyphContainer from "./TrendGlyphContainer";
import { getAnswers, getMiddleStockData, facetValues, getTrendAnswers } from "../lib/data";
import { TaskType, Encoding, Widget, TaskMode, TaskQuestions, TaskInstructions } from "../lib/experiment";

interface ExperimentTaskProps {
  betweenSubjectsParams: any;
  cancel?: () => void;
  isTraining: boolean;
  next: () => void;
  submitAnswer: (data: any) => void;
  taskReward?: number;
  withinSubjectsParams: any[];
  taskType: TaskType;
}

interface ExperimentTaskState {
  step: number;
  view: number;
  error: boolean;
  answer: string;
  widget: Widget;
  moneyEarned: number;
  taskStartTime: number;
  answerStartTime: number;
  answerEndTime: number;
}


export default class ExperimentTask extends React.Component<ExperimentTaskProps, ExperimentTaskState> {
  viz: MergedContainer;

  constructor(props: ExperimentTaskProps) {
    super(props);
    this.showAnswerBox = this.showAnswerBox.bind(this);
    this.submitAnswer = this.submitAnswer.bind(this);
    this.changeAnswer = this.changeAnswer.bind(this);
    this.changeAnswerTrend = this.changeAnswerTrend.bind(this);
    this.continue = this.continue.bind(this);
    this.cancel = this.cancel.bind(this);
    this.state = {
      step: 0,
      view: 0,
      error: false,
      answer: null,
      widget: Widget.Facet,
      moneyEarned: 0,
      taskStartTime: Date.now(),
      answerStartTime: null,
      answerEndTime: null,
    };
  }

  showAnswerBox() {
    this.setState(prevState => {
      return {
        view: prevState.view + 1,
        answerStartTime: Date.now(),
      };
    });
  }

  changeAnswer(event: any) {
    this.setState({ answer: event.target.value });
  }

  changeAnswerTrend(event: any) {
    this.setState({ answer: event.target.dataset.value});
  }

  submitAnswer() {
    this.setState(prevState => {
      const { step, view, answer, taskStartTime, answerStartTime } = prevState;
      const { isTraining, submitAnswer, taskType, withinSubjectsParams } = this.props;
      if (answer == null) {
        return { error: true };
      } else if (taskType === TaskType.EXTREMA) {
        const answerChoices = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
        if (answerChoices.indexOf(answer) === -1) {
          return { error: true };
        }
      }
      const answerEndTime = Date.now();
      const taskMode = isTraining ? TaskMode.TRAINING : TaskMode.TASK;
      const expected = getAnswers(step, taskMode, taskType);
      submitAnswer({
        bufferSize: withinSubjectsParams[step].bufferSize,
        dataset: getMiddleStockData(step, taskMode),
        color: withinSubjectsParams[step].color,
        delay: withinSubjectsParams[step].delay,
        encoding: Encoding[withinSubjectsParams[step].encoding],
        eventLog: this.viz.state.eventLog,
        expected,
        ordered: withinSubjectsParams[step].ordered,
        response: answer,
        responseEndTime: answerEndTime,
        taskNum: step,
        taskType: TaskType[taskType],
        vizEndTime: answerStartTime,
        vizStartTime: taskStartTime,
        widget: Widget[withinSubjectsParams[step].widget],
      });
      return {
        error: false,
        view: view + 1,
        answerEndTime,
      };
    });
  }

  continue() {
    if (this.state.step < this.props.withinSubjectsParams.length - 1) {
      this.setState((prevState, props) => {
        return {
          step: prevState.step + 1,
          view: 0,
          error: false,
          answer: null,
          moneyEarned: prevState.moneyEarned + props.taskReward,
          taskStartTime: Date.now(),
          answerStartTime: null,
          answerEndTime: null,
        };
      });
    } else {
      this.props.next();
    }
  }

  cancel() {
    const msg = `Are you sure you want to exit the HIT? You will only be paid $${this.state.moneyEarned.toFixed(2)} if you exit right now.`;
    if (confirm(msg)) {
      this.props.cancel();
    }
  }

  render() {
    const { betweenSubjectsParams, withinSubjectsParams, isTraining, taskType } = this.props;
    const { step, view, error, answer, widget, moneyEarned } = this.state;

    let task = null;
    let instructions = null;
    if (isTraining) {
      instructions = <p>You are not given any time limit, but you should <b>try to answer the question as quickly as possible</b>. Click on the "Ready to answer" button below to select your answer, but note that you may not continue interacting with the visualization when selecting your answer.</p>;
    }
    if (view !== 0) {
      instructions = <p>{TaskInstructions[taskType]}</p>;
      const invalid = error ? <p className="invalid">Invalid input</p> : null;
      let msg = null;
      if (view === 2) {
        if (isTraining) {
          // the id is not needed in this case (would be when we access more complex data)
          let answer = getAnswers(step, TaskMode.TRAINING, taskType);
          if (taskType === TaskType.TREND) {
            msg = (<p>
              Correct answer is highlighed in <span className="correct">green</span>.<br/>
              Click the "Continue to next task" button below to continue.
            </p>);
          } else {
            msg = (
              <p>
                Correct answer is <span className="correct">{answer}</span>.<br/>
                Click the "Continue to next task" button below to continue.
              </p>
            );
          }
        } else {
          msg = (
            <p>Answer submitted successfully, click the "Continue to next task" button below to continue.</p>
          );
        }
      }

      let answerInput;
      if (taskType === TaskType.THRESHOLD || taskType === TaskType.EXTREMA) {
        let answerValues;
        if (taskType === TaskType.THRESHOLD) {
          answerValues = ["YES", "NO"];
        } else {
          answerValues = facetValues;
        }

        let radioInputs = answerValues.map(ans => {
          return (
            <label key={ans + "_radio"}>
              <input
                type="radio"
                name="input"
                value={ans}
                checked={ans === answer}
                onChange={this.changeAnswer}
                disabled={view === 2}
              />
                {" " + ans}
            </label>
          );
        });
        answerInput = (
          <div className="radio-button-wrapper">{radioInputs}</div>
        );
      } else if (taskType === TaskType.TREND) {
        let taskMode = isTraining ? TaskMode.TRAINING : TaskMode.TASK;
        let trendinfo = getTrendAnswers(step, taskMode);
        answerInput = (
          <TrendGlyphContainer
            datasets={trendinfo.data}
            answerValues={trendinfo.answer}
            onClick={this.changeAnswerTrend}
            disabled={view === 2}
            isTraining={isTraining}
          />
        );
      }

      task = (
        <div>
          <div className="task-input">
            <label>Your answer: </label>
            {answerInput}
          </div>
          {invalid}
          {msg}
        </div>
      );
    }

    let taskTitle, taskName, taskMode, reward, cancelButton;
    if (isTraining) {
      taskTitle = "Training Task";
      taskName = "Training tasks";
      taskMode = TaskMode.TRAINING;
      reward = null;
      cancelButton = null;
    } else {
      taskTitle = "Task";
      taskName = "Tasks";
      taskMode = TaskMode.TASK;
      reward = <span>Current reward earned: ${moneyEarned.toFixed(2)}</span>;
      cancelButton = <button onClick={this.cancel} disabled={step === 0}>Finish and claim current reward</button>;
    }
    let question = TaskQuestions[taskType];

    return (
      <div>
        <h4>{taskTitle} {step + 1}</h4>
        <div className="reward-box">
          {taskName} completed: {step}/{withinSubjectsParams.length}
          <br/>
          {reward}
        </div>
        <p><strong>Task: {question}</strong></p>
        {instructions}
        <MergedContainer
          bufferSize={withinSubjectsParams[step].bufferSize}
          delay={withinSubjectsParams[step].delay}
          encoding={withinSubjectsParams[step].encoding}
          ref={v => this.viz = v}
          taskNum={step}
          widget={widget}
          disabled={view > 0}
          taskType={this.props.taskType}
          taskMode={taskMode}
          key={step}
          ordered={withinSubjectsParams[step].ordered}
          color={withinSubjectsParams[step].color}
        />
        {task}
        <div className="button-wrapper">
          <button onClick={this.showAnswerBox} disabled={view !== 0}>Ready to answer</button>
          <button onClick={this.submitAnswer} disabled={view !== 1}>Submit task</button>
          <button onClick={this.continue} disabled={view !== 2}>Continue to next task</button>
          {cancelButton}
        </div>
      </div>
    );
  }
}



