import * as React from "react";

import MergedContainer from "./MergedContainer";
import { TaskType, TaskMode, Encoding, Widget } from "../lib/experiment";

interface ExperimentTrainingProps {
  logData?: (data: any) => void;
  next: () => void;
  params?: any[];
}

interface ExperimentTrainingState {
  startTime: number;
  step: number;
  viewedVideo: number;
}

export default class ExperimentTraining extends React.Component<ExperimentTrainingProps, ExperimentTrainingState> {
  viz: MergedContainer;
  vid1: HTMLVideoElement;
  vid2: HTMLVideoElement;

  constructor(props: ExperimentTrainingProps) {
    super(props);
    this.onClick = this.onClick.bind(this);
    this.state = {
      startTime: Date.now(),
      step: 0,
      viewedVideo: -1,
    };
  }

  onClick() {
    const { next, logData, params } = this.props;
    const { startTime, step, viewedVideo } = this.state;
    const total = params.length;

    if (step === 3 && viewedVideo !== 1) {
      if (this.vid1.played.length === 0 || this.vid2.played.length === 0) {
        this.setState({ viewedVideo: 0 });
      } else {
        this.setState({ viewedVideo: 1 });
      }
      return;
    }

    logData({
      bufferSize: params[step].bufferSize,
      color: params[step].color,
      delay: params[step].delay,
      encoding: Encoding[params[step].encoding],
      eventLog: this.viz.state.eventLog,
      ordered: params[step].ordered,
      taskNum: step,
      vizEndTime: Date.now(),
      vizStartTime: startTime,
      widget: Widget[params[step].widget],
    });

    if (step < total - 1) {
      this.setState(prevState => {
        return {
          startTime: Date.now(),
          step: prevState.step + 1,
        };
      });
    } else {
      next();
    }
  }

  render() {
    const { params } = this.props;
    const { step, viewedVideo } = this.state;

    let instructions;
    if (step === 0) {
      instructions = (
        <div>
          <h4>Instructions Part 1</h4>
          <p>In these tasks you will be asked to read stock price data by interacting with a data visualization like the one shown below. You can hover over any of the months on the left, which will filter the data by the month selected. The corresponding chart will update accordingly. The chart shows the company's stock price for the years 2008-2012 for the given month selected. The horizontal axis displays the years, and the vertical axis displays the price in the selected month for that year.</p>
          <p><b>Spend some time familiarizing yourself with the visualization below</b>. The data shown here will be different from those shown in the tasks, however the visualization will remain the same. Make sure you understand how to read the data and interact with the visualization before proceeding.</p>
          <p>When you are finished, please click the "Next" button below.</p>
        </div>
      );
    } else if (step === 1) {
      instructions = (
        <div>
          <h4>Instructions Part 2</h4>
          <p>Now we have <b>increased the loading delay</b> compared to the previous visualization you saw, but the data is exactly the same. Spend some time interacting with the visualization below to familiarize yourself with the effects of the longer and varying delays.</p>
          <p>When you are finished, please click the "Next" button below.</p>
        </div>
      );
    } else if (step === 2) {
      instructions = (
        <div>
          <h4>Instructions Part 3</h4>
          <p>Now we have <b>increased the amount of data you can view at once</b> from one month to three months.</p>
          <p>We <b>recommend</b> trying to hover over multiple months at once and then viewing the results in the chart, rather than just loading and viewing the data one month at a time. Loading more data at once will not increase the total loading delay, and this can make it <b>much quicker</b> to read all the data.</p>
          <p>When you are ready, please click the "Next" button below.</p>
        </div>
      );
    } else if (step === 3 && viewedVideo !== 1) {
      let err = null;
      if (viewedVideo === 0) {
        err = <p className="invalid">You must play both of the videos above to proceed.</p>;
      }

      instructions = (
        <div>
          <h4>Instructions Part 4</h4>
          <p>Now that you have had some experience using the visualization under different settings, please view the following two videos to see different ways of interacting with the data visualization.</p>
          <p>Here is an example of loading and viewing the data one month at a time:</p>
          <video src="https://s3.amazonaws.com/jigsawexperiments/serial.mp4" controls width="550" ref={v => this.vid1 = v}></video>
          <p>Here is an example of loading multiple months at once and then viewing the results:</p>
          <video src="https://s3.amazonaws.com/jigsawexperiments/concurrent.mp4" controls width="550" ref={v => this.vid2 = v}></video>
          <p>Remember, we <b>recommend</b> trying to follow the behavior in the second video, since this can make reading all the data much quicker.</p>
          {err}
        </div>
      );
    } else if (step === 3) {
      instructions = (
        <div>
          <h4>Instructions Part 5</h4>
          <p>Now we have <b>increased the amount of data you can view at once</b> from three months to six months.</p>
          <p>We <b>recommend</b> trying to hover over multiple months at once and then viewing the results in the chart, rather than just loading and viewing the data one month at a time. Loading more data at once will not increase the total loading delay, and this can make it <b>much quicker</b> to read all the data.</p>
          <p>When you are ready, you can click the "Next" button below to start working on a set of training tasks before completing the rest of the tasks. In the training tasks, you will be shown the correct answer after submission. <b>Both the stock price data and the design of the visualization may change between tasks</b>.</p>
        </div>
      );
    }

    let viz =  (
      <MergedContainer
        bufferSize={params[step].bufferSize}
        delay={params[step].delay}
        encoding={params[step].encoding}
        ref={v => this.viz = v}
        taskNum={step}
        widget={params[step].widget}
        disabled={false}
        taskMode={TaskMode.INSTRUCTIONS}
        key={step}
        ordered={params[step].ordered}
        color={params[step].color}
      />
    );
    if (step === 3 && viewedVideo !== 1) {
      viz = null;
    }

    return (
      <div>
        {instructions}
        {viz}
        <div className="button-wrapper">
          <button onClick={this.onClick}>Next</button>
        </div>
      </div>
    );
  }
}



