import * as React from "react";

interface ExperimentUsabilityProps {
  onClick: any;
}

interface ExperimentUsabilityState {
  buffer: string;
  buffersize: string;
  delay: string;
  delaymultiple: string;
  comments: string;
  experience: string;
  error: boolean;
  disabled: boolean;
}

export default class ExperimentUsability extends React.Component<ExperimentUsabilityProps, ExperimentUsabilityState> {
  constructor(props: ExperimentUsabilityProps) {
    super(props);
    this.onChange = this.onChange.bind(this);
    this.onClick = this.onClick.bind(this);
    this.state = {
      buffer: null,
      buffersize: null,
      delay: null,
      delaymultiple: null,
      comments: "",
      experience: "",
      error: false,
      disabled: false,
    };
  }

  onChange(event: any) {
    this.setState({ [event.target.name]: event.target.value });
  }

  onClick(event: any) {
    this.setState(prevState => {
      const { buffer, buffersize, delay, delaymultiple, experience, comments } = prevState;
      if (buffer == null || buffersize == null || delay == null || delaymultiple == null) {
        return { error: true };
      } else {
        this.props.onClick({ buffer, buffersize, delay, delaymultiple, experience, comments });
        return { error: false, disabled: true };
      }
    });
  }

  render() {
    const { buffer, buffersize, delay, delaymultiple, comments, error, disabled } = this.state;

    const invalid = error ? <p className="invalid">You must complete all the required survey questions above to submit</p> : null;

    return (
      <div>
        <h4>Usability Survey</h4>
        <p>You have successfully completed all the visual analysis tasks. Before submitting this HIT, please complete the following usability survey corresponding to the tasks you just completed. When you are finished with the survey, you may click the "Finish HIT" button below to complete this HIT.</p>
        <label htmlFor="buffer">How much did you prefer viewing one month of data at once versus multiple months of data at once?:</label><br/>
        <div className="radio-button-wrapper">
          <span><label><input type="radio" name="buffer" value="1" checked={buffer === "1"} onChange={this.onChange}/> Strongly prefer one month at once</label></span>
          <span><label><input type="radio" name="buffer" value="2" checked={buffer === "2"} onChange={this.onChange}/> Slightly prefer one month at once</label></span>
          <span><label><input type="radio" name="buffer" value="3" checked={buffer === "3"} onChange={this.onChange}/> Neutral</label></span>
          <span><label><input type="radio" name="buffer" value="4" checked={buffer === "4"} onChange={this.onChange}/> Slightly prefer multiple months at once</label></span>
          <span><label><input type="radio" name="buffer" value="5" checked={buffer === "5"} onChange={this.onChange}/> Strongly prefer multiple months at once</label></span>
        </div>
        <br/>

        <label htmlFor="buffersize">How much did you prefer viewing three months of data at once versus six months of data at once?:</label><br/>
        <div className="radio-button-wrapper">
          <span><label><input type="radio" name="buffersize" value="1" checked={buffersize === "1"} onChange={this.onChange}/> Strongly prefer three months at once</label></span>
          <span><label><input type="radio" name="buffersize" value="2" checked={buffersize === "2"} onChange={this.onChange}/> Slightly prefer three months at once</label></span>
          <span><label><input type="radio" name="buffersize" value="3" checked={buffersize === "3"} onChange={this.onChange}/> Neutral</label></span>
          <span><label><input type="radio" name="buffersize" value="4" checked={buffersize === "4"} onChange={this.onChange}/> Slightly prefer six months at once</label></span>
          <span><label><input type="radio" name="buffersize" value="5" checked={buffersize === "5"} onChange={this.onChange}/> Strongly prefer six months at once</label></span>
        </div>
        <br/>
        <label htmlFor="delay">For visualizations that allowed viewing only one month of data at once, how much did loading delay affect the difficulty of using the visualization?</label><br/>
        <div className="radio-button-wrapper">
          <label><input type="radio" name="delay" value="1" checked={delay === "1"} onChange={this.onChange}/> No difficulty</label>
          <label><input type="radio" name="delay" value="2" checked={delay === "2"} onChange={this.onChange}/> Slight difficulty</label>
          <label><input type="radio" name="delay" value="3" checked={delay === "3"} onChange={this.onChange}/> Some difficulty</label>
          <label><input type="radio" name="delay" value="4" checked={delay === "4"} onChange={this.onChange}/> Large difficulty</label>
          <label><input type="radio" name="delay" value="5" checked={delay === "5"} onChange={this.onChange}/> Very large difficulty</label>
        </div>
        <br/>
        <label htmlFor="delaymultiple">For visualizations that allowed viewing multiple months of data at once, how much did loading delay affect the difficulty of using the visualization?</label><br/>
        <div className="radio-button-wrapper">
          <label><input type="radio" name="delaymultiple" value="1" checked={delaymultiple === "1"} onChange={this.onChange}/> No difficulty</label>
          <label><input type="radio" name="delaymultiple" value="2" checked={delaymultiple === "2"} onChange={this.onChange}/> Slight difficulty</label>
          <label><input type="radio" name="delaymultiple" value="3" checked={delaymultiple === "3"} onChange={this.onChange}/> Some difficulty</label>
          <label><input type="radio" name="delaymultiple" value="4" checked={delaymultiple === "4"} onChange={this.onChange}/> Large difficulty</label>
          <label><input type="radio" name="delaymultiple" value="5" checked={delaymultiple === "5"} onChange={this.onChange}/> Very large difficulty</label>
        </div>
        <br/>
        <label htmlFor="experience">Please describe your experience with the different visualization designs. Did you find any designs more difficult or easier to use in the face of loading delay? Did you react differently between tasks? Was anything unclear or particularly challenging? (optional):</label><br/>
        <textarea id="experience" name="experience" onChange={this.onChange}></textarea><br/><br/>
        <label htmlFor="comments">If you have any other comments about the tasks you just completed, the visualizations that you used, or the study procedure in general, please write them in the box below (optional):</label><br/>
        <textarea id="comments" name="comments" onChange={this.onChange}></textarea>
        {invalid}
        <div className="button-wrapper">
          <button onClick={this.onClick} disabled={disabled}>Finish HIT</button>
        </div>
      </div>
    );
  }
}



