/**
 * Returns a set of random parameters chosen from the list of all possible
 * combinations of parameters.
 */
export function getBetweenSubjectsParams(rng?: any) {
  const combos = [];
  for (let w of Object.keys(WidgetModes)) {
    for (let b of Object.keys(BlockingModes)) {
      for (let i of Object.keys(IndicatorModes)) {
        for (let c of Object.keys(CorrespondenceModes)) {
          for (let t of Object.keys(TaskModes)) {
            for (let dm of Object.keys(DataModes)) {
              combos.push({
                widgetMode: w,
                blocking: b,
                indicator: i,
                correspondence: c,
                taskMode: t,
                dataMode: dm,
              });
            }
          }
        }
      }
    }
  }
  const idx = getRandomInt(0, combos.length - 1, rng);
  // return {
  //   blocking: BlockingModes[combos[idx].blocking],
  //   indicator: IndicatorModes[combos[idx].indicator],
  //   correspondence: CorrespondenceModes[combos[idx].correspondence],
  //   taskMode: TaskModes[combos[idx].taskMode],
  //   dataMode: DataModes[combos[idx].dataMode],
  //   widgetMode: WidgetModes[combos[idx].widgetMode],
  // };
  return combos[idx];
}

/**
 * Returns the value associated with the given URL parameter name, otherwise
 * returns null if no such parameter value is set.
 * @param {string} sParam
 * @returns {string}
 */
export function getUrlParameter(sParam: string) {
  let sPageURL = decodeURIComponent(window.location.search.substring(1)),
    sURLVariables = sPageURL.split("&"),
    sParameterName,
    i;

  for (i = 0; i < sURLVariables.length; i++) {
    sParameterName = sURLVariables[i].split("=");

    if (sParameterName[0] === sParam) {
      return sParameterName[1] === undefined ? null : sParameterName[1];
    }
  }
  return null;
}

// Fisher-Yates shuffle
// optional pseudorandom number generator can be passed in
export function shuffle(array: any[], rng?: any) {
  let m = array.length, t, i;
  if (rng === undefined) {
    rng = Math.random;
  }

  // While there remain elements to shuffle
  while (m) {

    // Pick a remaining element
    i = Math.floor(rng() * m--);

    // And swap it with the current element.
    t = array[m];
    array[m] = array[i];
    array[i] = t;
  }

  return array;
}



